@use  'base/variables' as v;
@use  'base/mixins' as m;
@include m.telefono{ 
    .galeria{
    
        
            @include m.grid(2, 2rem);   
        
        
                img{
                    height: 20rem;
                    width: 20rem;
                    object-fit: cover;
                }  

        .imagen1{
            grid-row: 1 / 2;
            grid-column: 2 / 3;
            
        }
        .imagen2{
            grid-row: 1 / 3;
            grid-column: 1 / 2;
        
        }
        
        .imagen5{
            grid-row: 4 / 5;
            grid-column: 1 / 3;
            img{
                width:42rem;
            }
            
        }
        .imagen6{
            grid-row: 2 / 3;
        }
        .imagen7{
            grid-column: 1 / 2;
            grid-row: 2 / 3;
        
        }
    }
}
@include m.tablet{
    .galeria{
        img{
            height: 30rem;
            width: 30rem;
            object-fit: cover;
        }
        .imagen5{
            img{
                width: 62rem;
            }
        }
    }
    } 
@include m.desktop{ 
    .galeria{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        gap: 2rem;
        .imagen5{
            grid-row: 3 / 4;
            grid-column: 1 / 4;
            img{
                width: 94rem;
                margin: 0 1rem 0 2rem;
            }
        }
    }
}