@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido-contacto{

    @include m.tablet{ 
        @include m.grid(2, 1fr );
        gap: 2rem;
        background-image: url(../img/2.jpeg);
        background-repeat: no-repeat;
        background-position: right center;
        background-size: 50%;
    }
}
.formulario{
    .campo{
        margin-bottom: 2rem;
        &:last-of-type{
            margin-bottom: 0;
        }
        label{
            text-transform: uppercase;
            display: block;
            font-size: 2.2rem;
            font-weight: 700;
        }
        input:not([type="submit"]), textarea{
            width: 100%;
            padding: 1rem;
            border: .5rem solid v.$primario;
        }
        input[type="submit"]{
            background-color: v.$primario;
            width: 100%;
            border: none;
            padding: 1rem;
            color: v.$blanco;
            font-size: 2.2rem;
            font-weight: 700;
            text-transform: uppercase;
            &:hover{
                cursor: pointer;
            }
        }
    }
}