@use 'variables' as v;
h1, h2, h3{
    margin: 0 0 v.$separacion 0;
    font-family: v.$fuente_principal;
    text-transform: uppercase;
    text-align: center;
    line-height: 1.5;
}
h1{
    font-size: 4rem;
    &::after{
        content: '';
        display: block;
        width: 10rem;
        height: 10rem;
        margin: 0 auto;
        background-image: url(../img/116408.png);
        background-size: 10rem;
        filter: invert(100%);
    }
}   
h2{
    font-size: 3.6rem;
    color: v.$primario;
    display: block;

    
    &::after{
        content: '';
        display: block;
        width: 6rem;
        height: 6rem;
        margin: 0 auto;
        background-image: url(../img/116408.png);
        background-repeat: no-repeat;
        background-size: 5rem;
    }
    
}
.heading-blanco{
    @extend h2;
    color: v.$blanco;
    span{
        color: v.$blanco;
    }
    &::after{
        display: none;
    }
}
h3{
font-size: 3.2rem;
}