@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    background-color: v.$primario;

    .contenido-footer{
        padding: 5rem;
        @include m.tablet{ 
            @include m.grid(3, 2rem);   
        }
    }
    
    .copyright{
        text-align: center;
        margin: 0 ;
        color: v.$blanco;
        padding: 2rem;
    }
}
.footer-box{
    font-family: v.$fuente_cursiva;
    text-align: center;
    margin-bottom: v.$separacion;
    @include m.tablet{ 
        margin-bottom: 0;
    }
    h3{
        font-family: v.$fuente_cursiva;
        margin: 0 0 1rem 0;
        font-size: 2.4rem;
        color: v.$secundario;
    }
    p, a{
        font-size: 2rem;
        margin: 0;
        color: v.$blanco;
    }
    
    .fa-whatsapp{
        color: rgb(35, 122, 0);
        font-size: 6rem;
        width: 5rem;
        height: 5rem;
    }
    
    
}
.tel{
    &::after{
        content: '';
        display: block;
        width: 6rem;
        height: 6rem;
        margin: 0 auto;
        background-image: url(../img/phone-square-alt-solid.svg);
        background-repeat: no-repeat;
        background-size: 5rem;
    }
}


