html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
  background-color: #6e1618;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #000;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1, h2, .heading-blanco, h3 {
  margin: 0 0 5rem 0;
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}
h1::after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/116408.png);
  background-size: 10rem;
  filter: invert(100%);
}

h2, .heading-blanco {
  font-size: 3.6rem;
  color: #6e1618;
  display: block;
}
h2::after, .heading-blanco::after {
  content: "";
  display: block;
  width: 6rem;
  height: 6rem;
  margin: 0 auto;
  background-image: url(../img/116408.png);
  background-repeat: no-repeat;
  background-size: 5rem;
}

.heading-blanco {
  color: #fff;
}
.heading-blanco span {
  color: #fff;
}
.heading-blanco::after {
  display: none;
}

h3 {
  font-size: 3.2rem;
}

.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 767px) {
  .header {
    min-height: 70rem;
  }
}
.header::before {
  content: "";
  background-color: #000;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0.6;
}
.header .contenido-header {
  position: relative;
}
.header .contenido-header h1 {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  margin-top: 5rem;
}
@media (min-width: 767px) {
  .header .contenido-header h1 {
    margin-top: 25rem;
  }
}

.header-inicio {
  background-image: url(../img/3.jpeg);
}

.nav-principal {
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
}
@media (min-width: 767px) {
  .nav-principal {
    flex-direction: row;
    gap: 1rem;
    margin-top: 0;
  }
}
.nav-principal a {
  text-align: center;
  color: #fff;
  padding: 2rem 0 0 1.2rem;
  font-size: 2.4rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}
@media (min-width: 767px) {
  .nav-principal a {
    margin-bottom: 0;
  }
}
.nav-principal a:hover {
  color: #a76e6e;
}
.nav-principal a.activo {
  color: #a76e6e;
}

.barra {
  display: flex;
  flex-direction: column;
}
@media (min-width: 767px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 5rem;
  }
}
.barra .logo {
  display: flex;
  justify-content: center;
  flex-direction: row;
  padding-bottom: 3rem;
}
.barra .logo a {
  color: #fbfbfb;
  font-size: 6rem;
  height: 1rem;
}
.barra .logo .num {
  display: none;
  font-size: 4rem;
}
.barra .logo .whats {
  align-items: center;
}
@media (min-width: 767px) {
  .barra .logo {
    width: 20rem;
    margin: 0;
  }
  .barra .logo a {
    color: #fbfbfb;
    font-size: 6rem;
    width: 5rem;
    height: 5rem;
    justify-content: space-around;
    width: 1rem;
    padding: 3rem;
  }
  .barra .logo a:hover {
    color: #a76e6e;
  }
  .barra .logo .num {
    margin-left: 2rem;
  }
}
@media (min-width: 1024px) {
  .barra .logo .num {
    display: inline;
    margin: 0 4rem 0 0;
  }
}

.footer {
  background-color: #6e1618;
}
.footer .contenido-footer {
  padding: 5rem;
}
@media (min-width: 767px) {
  .footer .contenido-footer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer .copyright {
  text-align: center;
  margin: 0;
  color: #fff;
  padding: 2rem;
}

.footer-box {
  font-family: "Bona Nova", sans-serif;
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 767px) {
  .footer-box {
    margin-bottom: 0;
  }
}
.footer-box h3 {
  font-family: "Bona Nova", sans-serif;
  margin: 0 0 1rem 0;
  font-size: 2.4rem;
  color: #b3b3b3;
}
.footer-box p, .footer-box a {
  font-size: 2rem;
  margin: 0;
  color: #fff;
}
.footer-box .fa-whatsapp {
  color: #237a00;
  font-size: 6rem;
  width: 5rem;
  height: 5rem;
}

.tel::after {
  content: "";
  display: block;
  width: 6rem;
  height: 6rem;
  margin: 0 auto;
  background-image: url(../img/phone-square-alt-solid.svg);
  background-repeat: no-repeat;
  background-size: 5rem;
}

.contenido-principal {
  background-color: #fff;
  padding: 5rem 2rem;
}
@media (min-width: 767px) {
  .contenido-principal {
    padding: 10rem;
    margin-top: -10rem;
    position: relative;
  }
}

.iconos {
  margin-top: 5rem;
}
@media (min-width: 767px) {
  .iconos .listado-iconos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.iconos .listado-iconos li img {
  margin: 0 auto;
  width: 12rem;
}
.iconos .listado-iconos li p {
  font-family: "Bona Nova", sans-serif;
  font-size: 3.2rem;
  color: #6e1618;
}

.galeria {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 359px) {
  .galeria {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.galeria .imagen img {
  height: 30rem;
  width: 30rem;
  object-fit: cover;
}
@media (min-width: 1024px) {
  .galeria .imagen:nth-child(1) {
    grid-row: 1/3;
  }
  .galeria .imagen:nth-child(1) img {
    height: 62rem;
    object-position: center;
  }
  .galeria .imagen:nth-child(2) {
    grid-column: 2/4;
  }
  .galeria .imagen:nth-child(2) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(4) {
    grid-row: 2/4;
  }
  .galeria .imagen:nth-child(4) img {
    height: 62rem;
  }
  .galeria .imagen:nth-child(5) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(5) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(6) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(6) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(7) {
    grid-row: 3/5;
  }
  .galeria .imagen:nth-child(7) img {
    height: 62rem;
  }
  .galeria .imagen:nth-child(8) {
    grid-column: 2/5;
  }
  .galeria .imagen:nth-child(8) img {
    width: 94rem;
  }
  .galeria .imagen:nth-child(9) {
    grid-column: 1/3;
  }
  .galeria .imagen:nth-child(9) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(10) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(10) img {
    width: 62rem;
  }
}

@media (min-width: 767px) {
  .principal {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1fr;
    gap: 2rem;
  }
}
@media (min-width: 359px) {
  .galeria {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  .galeria img {
    height: 20rem;
    width: 20rem;
    object-fit: cover;
  }
  .galeria .imagen1 {
    grid-row: 1/2;
    grid-column: 2/3;
  }
  .galeria .imagen2 {
    grid-row: 1/3;
    grid-column: 1/2;
  }
  .galeria .imagen5 {
    grid-row: 4/5;
    grid-column: 1/3;
  }
  .galeria .imagen5 img {
    width: 42rem;
  }
  .galeria .imagen6 {
    grid-row: 2/3;
  }
  .galeria .imagen7 {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}
@media (min-width: 767px) {
  .galeria img {
    height: 30rem;
    width: 30rem;
    object-fit: cover;
  }
  .galeria .imagen5 img {
    width: 62rem;
  }
}
@media (min-width: 1024px) {
  .galeria {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
  .galeria .imagen5 {
    grid-row: 3/4;
    grid-column: 1/4;
  }
  .galeria .imagen5 img {
    width: 94rem;
    margin: 0 1rem 0 2rem;
  }
}
@media (min-width: 767px) {
  .contenido-contacto {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1fr;
    gap: 2rem;
    background-image: url(../img/2.jpeg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 50%;
  }
}

.formulario .campo {
  margin-bottom: 2rem;
}
.formulario .campo:last-of-type {
  margin-bottom: 0;
}
.formulario .campo label {
  text-transform: uppercase;
  display: block;
  font-size: 2.2rem;
  font-weight: 700;
}
.formulario .campo input:not([type=submit]), .formulario .campo textarea {
  width: 100%;
  padding: 1rem;
  border: 0.5rem solid #6e1618;
}
.formulario .campo input[type=submit] {
  background-color: #6e1618;
  width: 100%;
  border: none;
  padding: 1rem;
  color: #fff;
  font-size: 2.2rem;
  font-weight: 700;
  text-transform: uppercase;
}
.formulario .campo input[type=submit]:hover {
  cursor: pointer;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #6e1618;
}

.margin-negativo-10 {
  margin-top: -10rem;
}

.bg-white {
  background-color: #fff;
}

.padding-bottom-0 {
  padding-bottom: 0;
}