@use 'base/variables' as v;
@use 'base/mixins' as m;
.barra{
    display: flex;
    flex-direction: column;
    @include m.tablet{ 
        flex-direction: row;
        justify-content: space-between; 
        align-items: center;
        margin-top: 5rem;
    }
    .logo{
        
        display: flex;
        justify-content: center;
        flex-direction: row;
        padding-bottom: 3rem;
        
       
        a{
            color: rgb(251, 251, 251);
            font-size: 6rem;
           
            height: 1rem;
        }
        .num{
            display: none;
            font-size: 4rem;
        }
        .whats{
             align-items: center;
        }
        @include m.tablet{ 
            width: 20rem;
            margin: 0;
            a{
                color: rgb(251, 251, 251);
                font-size: 6rem;
                width: 5rem;
                height: 5rem;
                justify-content: space-around;
                width: 1rem;
                padding: 3rem;
                &:hover{
                    color: v.$botones;
                }
               
            }
            .num{
                margin-left: 2rem;

            }

        }
        @include m.desktop{ 
            .num{
                display:inline;
                margin: 0 4rem 0 0;
            }
        }
        
       
    }
    
}
