@use 'base/mixins' as m;
@use 'base/variables' as v;
.nav-principal{
    display: flex;
    flex-direction: column;
    margin-top: 5rem;
    @include m.tablet{ 
        flex-direction: row;  
        gap: 1rem; 
        margin-top: 0;
    }
    a{
        text-align: center;
        color: v.$blanco;
        padding: 2rem 0 0 1.2rem;
        font-size: 2.4rem;
        text-transform: uppercase;
        font-weight: 700;
        margin-bottom: 1rem;
        @include m.tablet{ 
            margin-bottom: 0;   
        }
        &:hover{
            color: v.$botones;;
        }
        &.activo{
            color: v.$botones;
        }
    }
    
}